<template>
  <div>
    <base-header class="pb-6" type="secondary">
      <div class="row align-items-center py-4">
        <div class="col-lg-6 col-7">
          <h6 class="h2 d-inline-block mb-0">Dashboard</h6>
          <!--          <nav aria-label="breadcrumb" class="d-none d-md-inline-block ml-md-4">-->
          <!--            <route-bread-crumb></route-bread-crumb>-->
          <!--          </nav>-->
        </div>
      </div>
      <!-- Card stats -->
      <div class="row">
        <div class="d-none d-md-block col-xl-3 col-md-6">
          <stats-card title="Lessons Viewed"
                      type="gradient-red"
                      sub-title="50"
                      icon="ni ni-active-40">

            <template slot="footer">
              <span class="text-success mr-2"><i class="fa fa-arrow-up"></i> 3.48%</span>
              <span class="text-nowrap">Since last month</span>
            </template>
          </stats-card>
        </div>
        <div class="d-none d-md-block col-xl-3 col-md-6">
          <stats-card title="Lessons Studied"
                      type="gradient-orange"
                      sub-title="24"
                      icon="ni ni-chart-pie-35">
            <template slot="footer">
              <span class="text-success mr-2"><i class="fa fa-arrow-up"></i> 12.18%</span>
              <span class="text-nowrap">Since last month</span>
            </template>
          </stats-card>
        </div>
        <div class="d-none d-md-block col-xl-3 col-md-6">
          <stats-card type="gradient-primary"
                      title="progress"
                      sub-title="67/150"
                      icon="ni ni-atom">
            <template slot="footer">
              <base-progress class="progress-xs mt-3 mb-0" type="success" :value="30"></base-progress>
            </template>
          </stats-card>
        </div>
        <div class="col-xl-3 col-md-6">
          <stats-card title="Current Level"
                      type="gradient-info"
                      sub-title="Newbie"
                      icon="ni ni-chart-bar-32">
            <template slot="footer">
              <base-button size="sm" type="primary" outline><i class="ni ni-settings-gear-65"></i>Change</base-button>
              <base-button size="sm" type="primary" outline><i class="ni ni-paper-diploma"></i>Assessment</base-button>
            </template>
          </stats-card>
        </div>
      </div>
    </base-header>

    <div class="container-fluid mt--6">

      <!--Widgets-->
      <div id="mainCourseArea">
        <div v-show="!mainCourse.lessonData" class="display-2 pt-6 pb-5">Retrieving Courses...</div>
        <transition name="fade">
          <div v-if="mainCourse.lessonData">
            <main-area :lessonData="mainCourse.lessonData"/>
            <div v-if="mainCourse.lessonData.length > 5">
              <lesson-deck
                :title="mainCourse.course.course_title"
                :lessonData="mainCourse.lessonData.slice(5,50)"
                :courseId="mainCourse.course.id"
                :viewMoreUrl="`https://chinesepod.com/library/courses/${mainCourse.course.course_title.split(' ').join('-')}/${mainCourse.course.id}`"
                @dropthis="dropThisCourse(mainCourse.course.id, 'main')"/>
            </div>
          </div>
        </transition>
      </div>
      <div v-if="courseData">
        <transition-group name="list" tag="div">
          <lesson-deck
            v-for="(course, index) in courseData.slice(0,10)"
            :key="index"
            v-if="course.lessonData.length > 0"
            :title="course.course.course_title"
            :lessonData="course.lessonData"
            :courseId="course.course.id"
            :viewMoreUrl="`https://chinesepod.com/library/courses/${course.course.course_title.split(' ').join('-')}/${course.course.id}`"
            @dropthis="dropThisCourse(course.course.id, 'course')"
            />
        </transition-group>
      </div>
      <div id="userBookmarks" v-show="bookmarkData !== mainCourse.lessonData">
        <div v-show="bookmarkSync" class="text-center">
          <i class="fas fa-spinner fa-spin m-5"></i>
        </div>
        <div v-if="!bookmarkData && !bookmarkSync" >
          <h2 class="display-3">Saved Lessons</h2>
          <div  class="text-center m-5">
            <h5>No bookmarked lessons just yet! Mark some lessons for later studies, and they'll appear here.</h5>
          </div>
        </div>
        <transition name="fade">
          <lesson-deck
            v-show="bookmarkData && !bookmarkSync"
            title="Saved Lessons"
            :lessonData="bookmarkData"
            viewMoreUrl="https://chinesepod.com/dashboard/selfstudy"/>
        </transition>
      </div>
      <div id="moreCourses">
        <transition name="fade">
          <course-deck
            v-show="moreCourses && !moreCoursesSync"
            title="Explore More Courses"
            :courseData="moreCourses"
            viewMoreUrl="https://chinesepod.com/library/courses/"
            @addedcourse="addCourse"
          />
        </transition>
      </div>
      <div id="latestLessons">
        <div v-show="latestSync" class="text-center">
          <i class="fas fa-spinner fa-spin m-5"></i>
        </div>
        <div v-if="!latestData && !latestSync">
          <h2 class="display-3">Latest Lessons</h2>
          <div class="text-center m-5">
            <h5>We weren't able to retrieve the latest lessons. Please refresh this page later.</h5>
          </div>
        </div>
        <transition name="fade">
          <lesson-deck
            v-show="latestData && !latestSync"
            title="Latest Lessons"
            :lessonData="latestData"
            viewMoreUrl="https://chinesepod.com/dashboard"/>
        </transition>
      </div>
      <div id="userHistory">
        <div v-show="historySync" class="text-center">
          <i class="fas fa-spinner fa-spin m-5"></i>
        </div>
        <div v-if="!historyData && !historySync">
          <h2 class="display-3">Lesson History</h2>
          <div  class="text-center m-5">
            <h5>You haven't studied any lessons yet! Mark a lesson as completed, and it will appear here.</h5>
          </div>
        </div>
        <transition name="fade">
          <lesson-deck
            v-show="historyData && !historySync"
            title="Lesson History"
            :lessonData="historyData"
            viewMoreUrl="https://chinesepod.com/dashboard/selfstudy"/>
        </transition>
      </div>
      <!--End Widgets-->

    </div>

  </div>
</template>
<script>


  // Components
  import BaseProgress from '@/components/BaseProgress';
  import RouteBreadCrumb from '@/components/Breadcrumb/RouteBreadcrumb';
  import StatsCard from '@/components/Cards/StatsCard';
  import LessonDeck from '@/components/Dashboard/LessonDeck'
  import CourseDeck from '@/components/Dashboard/CourseDeck'
  import MainArea from '@/components/Dashboard/MainArea'

  import axios from 'axios';

  import LessonData from '@/data/lessonData.json';

  export default {
    components: {
      BaseProgress,
      RouteBreadCrumb,
      StatsCard,
      LessonDeck,
      CourseDeck,
      MainArea
    },
    data() {
      return {
        courses: [],
        courseData: [],
        mainCourse: {},
        courseSync: true,
        bookmarkData: [],
        bookmarkSync: false,
        historyData: [],
        historySync: false,
        latestData: [],
        latestSync: false,
        moreCourses: [],
        moreCoursesSync: true,
        courseCleanup: []
      };
    },
    methods: {
      getUserCourses: async function () {
        // GET USER COURSES
        this.courses = await axios.get(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/user-courses')
          .then((data) => {return data.data})
          .catch(() => {});
        // IF NO COURSES - GET DUMMY COURSE
        if (this.courses.length === 0 && this.bookmarkData.length > 0) {
          this.mainCourse = {
            course: {
              course_title: 'Your Bookmarked Lessons',
              bookmarks: true
            },
            lessonData: this.bookmarkData
          }
        } else {
          // GET MAIN COURSE
          try {
            this.mainCourse = {
              course: this.courses[0]['course'],
              lessonData: await this.getCourseData(this.courses[0]['course'].id)
            };
          } catch (e) {
          }
        }
        // CHECK IF MAIN COURSE IS EMPTY
        if (this.mainCourse.lessonData && this.mainCourse.lessonData.length > 0) {
          // GET OTHER COURSES
          this.courses.slice(1,5).forEach( async (course) => {
            if (course.course) {
              this.courseData.push({
                course: course.course,
                lessonData: await this.getCourseData(course.course.id)
              });
            }
          });
        } else {
          // FIND NEXT COURSE IF AVAILABLE
          if (this.courses.length > 1) {
            this.mainCourse = {
              course: this.courses[1]['course'],
              lessonData: await this.getCourseData(this.courses[1]['course'].id)
            };

            // GET OTHER COURSES
            this.courses.slice(2,6).forEach( async (course, index) => {
              this.courseData.push({
                course: course.course,
                lessonData: await this.getCourseData(course.course.id)
              });
            });

          } else {
            // LEAVE EMPTY
            this.mainCourse = {
              course: {
                course_title: 'All The Way to Intermediate'
              },
              lessonData: await this.getCourseData(951)
            };
          }
        }
      },
      getCourseData: async function (courseId) {
        return await axios.get(process.env.VUE_APP_ROOT_API + `/api/v1/dashboard/course-lessons?courseId=${courseId}&limit=20`)
          .then( async (data) => {
            // console.log(data.data);
            if (data.data.length > 0) {
              return data.data
            } else {
              //TODO UNSUB USER HERE
              axios.post(process.env.VUE_APP_ROOT_API + `/api/v1/dashboard/toggle-course`, {
                courseId: courseId,
                status: false
              })
                .catch(() => {});
              return []
            }
          })
          .catch(() => {});
      },
      addCourse: async function (course) {
        await axios.get(process.env.VUE_APP_ROOT_API + `/api/v1/dashboard/course-lessons?courseId=${course.id}&limit=20`)
          .then((data) => {
            this.courseData.push({
              course: course,
              lessonData: data.data
            })
          })
          .catch(() => {});
      },
      getMoreCourses: async function () {
        this.moreCoursesSync = true;
        this.moreCourses = await axios.get(process.env.VUE_APP_ROOT_API + `/api/v1/dashboard/more-courses`)
          .then((data) => {
            return data.data
          }).catch(() => {});
        this.moreCoursesSync = false;
      },
      updateLatestLessons: async function () {
        this.latestSync = true;
        this.latestData = await axios.get(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/all-lessons?limit=20')
          .then((data) => {
            return data.data
          })
          .catch(() => {});
        this.latestSync = false;
      },
      updateBookmarks: async function () {
        this.bookmarkSync = true;
        this.bookmarkData= await axios.get(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/bookmarks?limit=30')
          .then((lessonData) => {
            return lessonData.data;
          })
          .catch(() => {});
        this.bookmarkSync = false;
      },
      updateHistory: async function () {
        this.historySync = true;
        this.historyData = await axios.get(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/history?limit=30')
          .then((lessonData) => {
            return lessonData.data;
          })
          .catch(() => {});
        this.historySync = false;
      },
      dropThisCourse(courseId, area) {
        if (area === 'main') {
          if (this.courseData.length > 0) {
            this.mainCourse = this.courseData[0];
            if (this.courseData.length > 1) {
              this.courseData = this.courseData.slice(1, 5);
            } else {
              this.courseData = []
            }
            // this.$forceUpdate();
          } else if (this.bookmarkData) {
            this.mainCourse = {
              course: {course_title: 'Your Bookmarked Lessons'},
              lessonData: this.bookmarkData
            }
          } else {
            this.mainCourse = []
          }
        } else if (area === 'course') {
          // console.log(area);
          this.courseData = this.courseData.filter((course) => {return course.course.id !== courseId});
          // this.$forceUpdate();
        }
        // console.log(courseId);
        // console.log('Receiving Emit')
      }
    },
    mounted() {
      this.getUserCourses();
      this.updateBookmarks();
      this.getMoreCourses();
      this.updateLatestLessons();
      this.updateHistory();
    }
  };
</script>
