<template>
  <a :href="`https://chinesepod.com/lessons/${lesson.slug}`">
    <div  class="card bg-transparent shadow-none border-0 mb-0">
      <img
        class="card-img"
        v-lazy="lesson.id[0] === 'Q'
        ? `${qingWen}`
        : `https://s3contents.chinesepod.com${lesson.type === 'extra' ? '/extra/' : '/'}${lesson.id}/${lesson.hash_code}/${lesson.image}`"
        :alt="lesson.title"
      >
      <div class="card-img-overlay rounded shadow-none" :class="[lesson.studied ? 'bg-translucent-dark' : 'bg-transparent']">
        <div class="float-right">
          <button
            class="btn btn-secondary btn-circle btn-circle-sm mt--4 mr-1"
            :class="[lesson.saved ? 'text-danger' : '']"
            @click.prevent="markSaved(lesson)"
          >
            <i
              class="fa-bookmark"
              :class="[lesson.saved ? 'fas' : 'far']"
            ></i>
          </button>
          <button
            class="btn btn-secondary btn-circle btn-circle-sm mt--4 mr--3"
            :class="[lesson.studied ? 'text-danger' : '']"
            @click.prevent="markStudied(lesson)"
          ><i class="fas fa-check"></i></button>
        </div>
      </div>
    </div>
    <div class="card-body p-0 pb-4">
      <h2 class="card-title m-0">{{ lesson.title }}</h2>
    </div>
  </a>
</template>
<script>
  import axios from 'axios'

  import qingWen from '@/assets/img/qing-wen.jpg'

  export default {
    name: "lessonCard",
    props: {
      lesson: {
        type: Object,
        description: "Lesson data to show"
      },
    },
    data () {
      return {
        qingWen: qingWen
      }
    },
    methods: {
      markStudied: async function (lesson) {
        lesson.studied = !lesson.studied;
        this.$forceUpdate();
        await axios.post(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/toggle-studied', {
          lessonId: lesson.id,
          status: lesson.studied
        }).catch(() => {});

      },
      markSaved: async function (lesson) {
        lesson.saved = !lesson.saved;
        this.$forceUpdate();
        await axios.post(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/toggle-saved', {
          lessonId: lesson.id,
          status: lesson.saved
        }).catch(() => {});
      }
    },
  }
</script>
<style lang="scss" scoped>
  .btn-circle {
    width: 45px;
    height: 45px;
    line-height: 45px;
    text-align: center;
    padding: 0;
    border-radius: 50%;
  }
  .btn-circle i {
    position: relative;
    top: -1px;
  }
  .btn-circle-sm {
    width: 35px;
    height: 35px;
    line-height: 35px;
    font-size: 0.9rem;
  }
</style>
